@font-face {
  font-family: "PT Sans";
  src: url("../../fonts/ptsans.woff2") format("woff2"), url("../../fonts/ptsans.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  src: url("../../fonts/ptsansbold.woff2") format("woff2"), url("../../fonts/ptsansbold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

img {
  display: block;
  object-fit: contain;
}

html {
  height: 100%;
}

button {
  padding: 0;
  font: inherit;
  border: none;
  cursor: pointer;
}

ul,
ol {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

a {
  display: block;
  color: inherit;
  text-decoration: none;
}

input {
  display: block;
  padding: 0;
  font: inherit;
  border: none;
  outline: none;
}

h1,
h2,
h3,
p {
  margin: 0;
}

fieldset,
textarea {
  border: 0;
  padding: 0;
  margin: 0;
}

textarea {
  resize: none;
}

legend {
  padding: 0;
}

video {
  display: block;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

@media (min-width: 768px) {
  .line-break--mobile {
    display: none;
  }
}
@media (max-width: 767px), (min-width: 1200px) {
  .line-break--tablet {
    display: none;
  }
}
@media (max-width: 1199px) {
  .line-break--desktop {
    display: none;
  }
}
@media (min-width: 1200px) {
  .line-break--mob-tab {
    display: none;
  }
}
@media (min-width: 768px) and (max-width: 1199px) {
  .line-break--mob-ds {
    display: none;
  }
}
@media (max-width: 767px) {
  .line-break--tab-ds {
    display: none;
  }
}

