.button {
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-align: center;
  text-transform: uppercase;
  color: #ffffff;
  background-color: #81b3d2;
  box-shadow: 0 1px 0 rgba(1, 1, 1, 0.2), inset 0 1px 0 rgba(255, 255, 255, 0.2);
}
.button:hover {
  background-color: #669ec0;
}
.button:active {
  background-color: #5496bd;
}
.button:disabled {
  background-color: #e5e5e5;
  cursor: default;
}

.main-header {
  position: relative;
}

.main-header__logo {
  position: absolute;
  z-index: 1;
  left: calc(50% - 92.5px);
}
@media (min-width: 768px) {
  .main-header__logo {
    z-index: 3;
  }
}

.main-nav {
  min-height: 56px;
}

.main-nav__toggle {
  position: absolute;
  z-index: 3;
  top: 15px;
  right: 19px;
  width: 23px;
  height: 22px;
  background-color: transparent;
}
.main-nav__toggle:hover {
  opacity: 0.6;
}
.main-nav__toggle:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}

.main-nav--closed .main-nav__burger {
  display: block;
  position: absolute;
  left: 0;
  width: 23px;
  height: 4px;
  background-color: #81b3d2;
}
.main-nav--closed .main-nav__burger::before, .main-nav--closed .main-nav__burger::after {
  display: block;
  content: "";
  position: absolute;
  left: 0;
  width: 23px;
  height: 4px;
  background-color: #81b3d2;
}
.main-nav--closed .main-nav__burger::before {
  transform: translateY(10px);
}
.main-nav--closed .main-nav__burger::after {
  transform: translateY(-10px);
}

.main-nav--opened .main-nav__burger::before, .main-nav--opened .main-nav__burger::after {
  z-index: 3;
  display: block;
  content: "";
  position: absolute;
  left: 0;
  width: 25px;
  height: 4px;
  background-color: #ffffff;
}
.main-nav--opened .main-nav__burger::before {
  transform: rotate(45deg);
}
.main-nav--opened .main-nav__burger::after {
  transform: rotate(-45deg);
}

.main-nav--nojs .main-nav__toggle {
  display: none;
}
.main-nav--nojs .main-nav__list {
  position: static;
}

.main-nav--closed .site__list {
  display: none;
}

.main-nav--opened.main-nav--nojs .site__list {
  position: static;
}

.site-list {
  position: absolute;
  width: 100%;
}
@media (min-width: 768px) {
  .site-list {
    position: static;
    display: flex;
    flex-wrap: wrap;
  }
}

